/*=============================
	Blog One CSS
===============================*/
.blog-one {
  position: relative;
  display: block;
  padding: 120px 0 117px;
  z-index: 1;
}

.blog-one__pattern {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-attachment: scroll;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.05;
  z-index: -1;
}

.blog-one__top {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-bottom: 58px;
}

.blog-one__top .sec-title {
  padding-bottom: 0px;
}

.blog-one__top .btn-box {
  position: relative;
  display: block;
  top: -13px;
}

.blog-one__bottom {
  position: relative;
  display: block;
}

.blog-one__single {
  position: relative;
  display: block;
  padding-right: 20px;
  padding-bottom: 20px;
  margin-bottom: 45px;
  z-index: 1;
}

.blog-one__single::before {
  position: absolute;
  top: 20px;
  left: 20px;
  bottom: 0;
  right: 0;
  background: rgba(var(--thm-secondary-rgb), 0.06);
  border-radius: 4px;
  transition: all 500ms linear;
  transition-delay: 0.1s;
  content: "";
  z-index: -1;
}

.blog-one__single:hover::before {
  background: var(--thm-secondary);
}

.blog-one__single::after {
  position: absolute;
  top: 30px;
  left: 0;
  bottom: 20px;
  right: 30px;
  border: 1px solid var(--thm-primary);
  border-radius: 4px;
  transition: all 500ms linear;
  transition-delay: 0.1s;
  content: "";
  z-index: -2;
}

.blog-one__single:hover::after {
  border-color: var(--thm-secondary);
}

.blog-one__single-img {
  position: relative;
  display: block;
  z-index: 5;
}

.blog-one__single-img .inner {
  position: relative;
  display: block;
  overflow: hidden;
  border-radius: 4px 4px 0px 0px;
}

.blog-one__single-img .inner:before {
  background: rgba(255, 255, 255, 0.3);
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 0;
  opacity: 1;
  z-index: 9;
  pointer-events: none;
}

.blog-one__single:hover .blog-one__single-img .inner:before {
  height: 100%;
  opacity: 0;
  -webkit-transition: all 400ms linear;
  transition: all 400ms linear;
}

.blog-one__single-img .inner .text-box {
  position: absolute;
  top: 20px;
  left: 20px;
  color: var(--thm-primary);
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  background: var(--thm-secondary);
  border-radius: 4px;
  font-family: var(--thm-heading-font);
  padding: 4px 10px 2px;
  z-index: 7;
}

.blog-one__single-img .inner img {
  width: 100%;
  transition: 0.5s ease;
  transform: scale(1.05);
}

.blog-one__single:hover .blog-one__single-img .inner img {
  transform: scale(1);
}

.blog-one__single-content {
  position: relative;
  display: block;
  padding: 31px 50px 31px;
  padding-right: 30px;
  z-index: 1;
}

.blog-one__single-content::before {
  position: absolute;
  top: 0;
  left: 19px;
  bottom: 0;
  right: 0;
  background: var(--thm-white);
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  content: "";
  z-index: -1;
}

.blog-one__single-content h2 {
  font-size: 24px;
  font-weight: 500;
  line-height: 36px;
  text-transform: capitalize;
}

.blog-one__single-content h2 a {
  color: var(--thm-heading-color);
  background: linear-gradient(to right, currentcolor 0%, currentcolor 100%) 0px
    95% / 0px 1px no-repeat;
  color: inherit;
  transition: all 500ms ease;
}

.blog-one__single-content h2 a:hover {
  background-size: 100% 1px;
}

.blog-one__single-content .meta-box {
  position: relative;
  display: block;
  overflow: hidden;
  margin-top: 17px;
}

.blog-one__single-content .meta-box li {
  position: relative;
  display: block;
  float: left;
  color: #93999f;
  font-size: 16px;
  line-height: 28px;
  font-weight: 500;
  text-transform: capitalize;
}

.blog-one__single-content .meta-box li + li {
  margin-left: 5px;
}

.blog-one__single-content .meta-box li a {
  color: var(--thm-heading-font-color);
}

.blog-one__single-content .meta-box li a:hover {
  color: var(--thm-primary);
}

.blog-one .swiper-container {
  padding-left: 1px;
}

#blog-one__pagination {
  position: relative;
  padding-top: 0px;
  z-index: 55;
}

#blog-one__pagination .swiper-pagination-bullet {
  position: relative;
  width: 8px;
  height: 8px;
  margin: 0px 10px;
  background: #d0dadc;
  border-radius: 50%;
  opacity: 1;
  transition: all 200ms linear;
  transition-delay: 0.1s;
}

#blog-one__pagination
  .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: var(--thm-secondary);
}

#blog-one__pagination .swiper-pagination-bullet::before {
  position: absolute;
  top: -6px;
  left: -6px;
  bottom: -6px;
  right: -6px;
  border: 1px solid var(--thm-secondary);
  border-radius: 50%;
  transform: scaleX(0);
  transition: all 200ms linear;
  transition-delay: 0.1s;
  content: "";
}

#blog-one__pagination
  .swiper-pagination-bullet.swiper-pagination-bullet-active::before,
#blog-one__pagination .swiper-pagination-bullet:hover {
  transform: scaleX(1);
}

