.sidebar {
  position: relative;
  display: block;
  margin-left: 0px;
}
.justify {
  text-align: justify;
}
.sidebar__single {
  position: relative;
  display: block;
}

.sidebar__single + .sidebar__single {
  margin-top: 30px;
}

.sidebar__single .title-box {
  position: relative;
  display: block;
  margin-top: -9px;
  margin-bottom: 25px;
}

.sidebar__single .title-box h2 {
  font-size: 30px;
  line-height: 40px;
  font-weight: 500;
  text-transform: capitalize;
}

.sidebar__search {
  position: relative;
  display: block;
  background: #f7f7f7;
  border-radius: 4px;
  padding: 35px 30px 35px;
  z-index: 1;
}

.sidebar__search-form {
  position: relative;
  display: block;
}

.sidebar__search-form input[type="search"] {
  position: relative;
  display: block;
  border: none;
  outline: none;
  background-color: var(--thm-white);
  color: #6f7174;
  font-size: 16px;
  font-weight: 500;
  padding-left: 20px;
  height: 55px;
  width: 100%;
  padding-right: 60px;
  border-radius: 4px;
}

.sidebar__search-form ::-webkit-input-placeholder {
  color: #6f7174;
}

.sidebar__search-form :-ms-input-placeholder {
  color: #6f7174;
}

.sidebar__search-form ::-ms-input-placeholder {
  color: #6f7174;
}

.sidebar__search-form ::placeholder {
  color: #6f7174;
}

.sidebar__search-form :-ms-input-placeholder {
  color: #6f7174;
}

.sidebar__search-form ::-ms-input-placeholder {
  color: #6f7174;
}

.sidebar__search-form button[type="submit"] {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  width: 60px;
  background-color: var(--thm-primary);
  color: var(--thm-black);
  font-size: 20px;
  outline: none;
  border: none;
  border-radius: 4px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-content: center;
  padding: 0;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
}

.sidebar__search-form button i {
  font-weight: 700;
}

.sidebar__search-form button:hover {
  color: var(--thm-white);
  background: var(--thm-black);
}

.sidebar__services {
  position: relative;
  display: block;
  background: #f7f7f7;
  border-radius: 4px;
  padding: 35px 30px 35px;
}

.sidebar__services-list {
  position: relative;
  display: block;
}

.sidebar__services-list li {
  position: relative;
  display: block;
  margin-bottom: 10px;
}

.sidebar__services-list li:last-child {
  margin-bottom: 0;
}

.sidebar__services-list li a {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--thm-white);
  border-radius: 4px;
  color: #6f7174;
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
  text-transform: capitalize;
  padding: 13px 20px 12px;
  font-family: var(--thm-body-font);
  transition: all 200ms linear;
  transition-delay: 0.1s;
  z-index: 1;
}

.sidebar__services-list li a:hover,
.sidebar__services-list li a.active {
  color: var(--thm-white);
}

.sidebar__services-list li a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0px;
  right: 0;
  border-radius: 4px;
  background-color: var(--thm-primary);
  transition: -webkit-transform 0.4s ease;
  transition: transform 0.4s ease;
  transition: transform 0.4s ease, -webkit-transform 0.4s ease;
  transform-origin: bottom right;
  -webkit-transform: scale(1, 0);
  transform: scale(1, 0);
  z-index: -1;
}

.sidebar__services-list li a:hover::before,
.sidebar__services-list li a.active::before {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  transform-origin: top center;
